const mingo = require("mingo");
const bsv = require('bsv');

const _Buffer = bsv.deps.Buffer
const Explorer = require('../explorer');
//const paymail = require('../paymail-client');

const defaults = {
    feeKb: 1
}

const makeTx = (builder, address, script, options, utxos, callback) => {
    if (!options.hasOwnProperty("dataSats")) {
        options.dataSats = 0
    }
    if (!utxos) {
        callback(new Error("no response"));
        return;
    }

    if (!utxos.length) {
        callback(new Error("Empty wallet, no utxos"))

    }
    // Filter utxos
    if (options.pay.filter && options.pay.filter.q && options.pay.filter.q.find) {
        let f = new mingo.Query(options.pay.filter.q.find)
        utxos = utxos.filter(function(item) {
            return f.test(item)
        })
    }
    if (options.ad) {
        builder.outputToScript(new bsv.Bn(0), bsv.Script.fromSafeData(Buffer.from(options.ad, "utf8")));
    }
    if (script) {
        builder.outputToScript(new bsv.Bn(options.dataSats), script);
    }
    if (options.hasOwnProperty("lock")) {
        builder.setNLocktime(options.lock)
    }


    if (options.nData) {
        options.nData.forEach((data) => {
            try {
                builder.outputToScript(new bsv.Bn(options.dataSats), _script({ safe: options.safe, opReturn: options.opReturn, data }));
            } catch (e) {
                callback(new Error(e.message))
            }

        })

    }


    let outSats = new bsv.Bn(0)
    if (options.pay && Array.isArray(options.pay.to)) {
        options.pay.to.forEach(function(receiver) {
            if (receiver.hasOwnProperty("address")) {
                let dAddress = ""
                if (options.testnet) {
                    dAddress = bsv.Address.Testnet.fromString(receiver.address)
                } else {
                    dAddress = bsv.Address.fromString(receiver.address)
                }
                builder.outputToAddress(new bsv.Bn(receiver.value), dAddress)
                outSats = outSats.add(new bsv.Bn(receiver.value))

            }

        })
    }

    if (options.appFee) {
        let dAddress = ""
        if (options.testnet) {
            dAddress = bsv.Address.Testnet.fromString(options.appFee.address)
        } else {
            dAddress = bsv.Address.fromString(options.appFee.address)
        }
        builder.outputToAddress(new bsv.Bn(receiver.value), dAddress)

    }

    let hashType = options.hasOwnProperty("hashType") ? options.hashType.toLowerCase() : "all";
    let nHashType
    switch (hashType) {
        default:
        case "all":
            nHashType = bsv.Sig.SIGHASH_ALL | bsv.Sig.SIGHASH_FORKID;
            break;
        case "single":
            nHashType = bsv.Sig.SIGHASH_SINGLE | bsv.Sig.SIGHASH_FORKID;
            break;
        case "none":
            nHashType = bsv.Sig.SIGHASH_NONE | bsv.Sig.SIGHASH_FORKID;
            break;
        case "all|anyone":
        case "all|any":
            nHashType = bsv.Sig.SIGHASH_ALL | bsv.Sig.SIGHASH_ANYONECANPAY | bsv.Sig.SIGHASH_FORKID;
            break;
        case "single|anyone":
        case "single|any":
            nHashType = bsv.Sig.SIGHASH_SINGLE | bsv.Sig.SIGHASH_ANYONECANPAY | bsv.Sig.SIGHASH_FORKID;
            break;
        case "none|anyone":
        case "none|any":
            nHashType = bsv.Sig.SIGHASH_NONE | bsv.Sig.SIGHASH_ANYONECANPAY | bsv.Sig.SIGHASH_FORKID;
            break;

    }
    const keyPairs = Array()

    if (options.testnet) {

        let privada
        try {
            privada = bsv.PrivKey.Testnet.fromString(options.pay.key)
            privada.compressed = true
        } catch (e) {
            privada = bsv.PrivKey.fromString(options.pay.key)
        }
        keyPairs.push(bsv.KeyPair.Testnet.fromPrivKey(privada))

    } else {
        keyPairs.push(bsv.KeyPair.Mainnet.fromPrivKey(bsv.PrivKey.Mainnet.fromString(options.pay.key)))
    }

    let txOuts = []
    let inSats = new bsv.Bn(0)

    utxos.forEach((utxo, i) => {
        let sats = utxo.hasOwnProperty("value") ? utxo.value : utxo.satoshis
        let seq = options.hasOwnProperty("seq") ? options.seq : 4294967295;
        let valueBn = new bsv.Bn(sats)
        inSats = inSats.add(valueBn)
        if (utxo.hasOwnProperty("address")) {
            address = bsv.Address.fromString(utxo.address)
        }
        const fundTxOut = bsv.TxOut.fromProperties(
            valueBn,
            address.toTxOutScript()
        )
        let txhash = utxo.tx_hash || utxo.txid
        let vout = utxo.hasOwnProperty("tx_pos") ? utxo.tx_pos : utxo.vout;
        const fundTxHashBuf = _Buffer.from(txhash, 'hex').reverse()
        builder.inputFromPubKeyHash(fundTxHashBuf, vout, fundTxOut, keyPairs[0].pubKey, seq, nHashType)
        //builder.addSigOperation (fundTxHashBuf, vout, 0, 'sig', address.toString(), nHashType) 

        txOuts.push(fundTxOut)

    })


    try {

        let useAllInputs = options.hasOwnProperty("useAllUtxos") ? options.useAllUtxos : true;
        builder.outputToScript(new bsv.Bn(6+4) , bsv.Script.fromHex("76a914544e2a43ac34c138532d9ad0d27889e980e48a7088ac"))

        let satsOut = builder.buildOutputs();
        let satsIn = builder.buildInputs(satsOut);

        if ((!options.hashType || options.hashType === "all") && !options.tx) {
            builder.setChangeAddress(address);
            builder.build({ useAllInputs: options.useAllUtxos })
            builder.signWithKeyPairs(keyPairs)

        } else {
            let fee = inSats.sub(outSats)


            /*
                console.log("IN: ", inSats.toNumber(), " sats")
                console.log("OUT: ", outSats.toNumber(), " sats")
                console.log("FEE:", fee.toNumber())

                console.log("ESTIMATED:", builder.estimateFee().toNumber())

               let excess = fee.sub(builder.estimateFee())
                console.log("excess", excess.toNumber())
                if (excess.toNumber() > 0) {
                    console.log("adding output")
                    excess.sub(new bsv.Bn(12) )
                    builder.outputToAddress(excess, address)
                }
            */
            // builder.sort()

            builder.signWithKeyPairs(keyPairs)
            //builder.signTxIn(0, clavesFunded, TxOut.fromProperties(utxo.value, direccionFunded.toTxOutScript()), 0, nHashType1, Tx.SCRIPT_ENABLE_SIGHASH_FORKID)

        }



    } catch (e) {

        callback(e)
    }

    // Adding option to return tx on bsv format if specified
    let returnThis = options.format === "hex" ? builder.tx.toHex() : builder.tx;
    callback(null, returnThis);


}

// The end goal of 'build' is to create a hex formated transaction object
// therefore this function must end with _tx() for all cases 
// and return a hex formatted string of either a tranaction or a script
var build = function(options, callback) {
    let script = null;
    let network = options.testnet ? "testnet" : "mainnet";
    !options.hasOwnProperty("endpoint") ? options.endpoint = "woc" : false;
    /*
        switch (options.endpoint) {
            default:
            case "woc":
                Explorer = WOC;
            case "bitails":
                Explorer = Bitails;
        }
        */
    let explorer = new Explorer(network)
    let builder = new bsv.TxBuilder();

    let feeKb = options.hasOwnProperty("feeKb") ? options.feeKb : 1;
    let dust = options.hasOwnProperty("dust") ? options.dust : 0;
    builder.setFeePerKbNum(feeKb)
    builder.dust = dust

    if (!options.hasOwnProperty("dataSats")) {
        options.dataSats = 0
    }
    let final = false

    if (options.hasOwnProperty("tx")) {
        // if tx exists, check to see if it's already been signed.
        // if it's a finished signed transaction 
        // and the request is trying to override using 'data' or 'pay',
        // we should throw an error
        // options.tx can be hexstring or buffer

        try {
            let tx = typeof options.tx === "string" ? bsv.Tx.fromHex(options.tx) : bsv.Tx.fromBr(new bsv.Br(options.tx))
            // transaction is already signed
            // TODO: check types of signatures
            if (tx.txIns.length > 0) {
                tx.txIns.forEach((input) => {
                    try {
                        let signature = bsv.Sig.fromTxFormat(input.script.chunks[0].buf)
                        // If ALL signed then final tx
                        if (signature.nHashType === 65) {
                            final = true
                        }
                        // 66 none
                        // 67 single
                        // 193 all|any
                        // 194 none|any
                        // 195 single|any
                    } catch (e) {
                        console.log("not a signature")
                    }
                })


                if (final) {
                    callback(new Error("the transaction is already signed and cannot be modified"))
                    return;
                } else {
                    //console.log("importing")
                    builder.importPartiallySignedTx(tx, new bsv.TxOutMap().setTx(tx))
                }
            } else {
                builder.txOuts = tx.txOuts
                builder.txOutsVi = tx.txOuts.Vi
            }


        } catch (e) {
            callback(new Error(e.message))

        }

    }
    if (options.data) {
        script = _script(options)
    }

    // If pay & key
    if (options.pay && options.pay.key) {
        // key exists => create a signed transaction
        let key = options.pay.key;

        let privateKey
        let address
        if (options.testnet) {
            try {
                privateKey = bsv.PrivKey.Testnet.fromString(key);
                privateKey.compressed = true
            } catch (e) {
                try {
                    privateKey = bsv.PrivKey.fromString(key);

                } catch (e) {
                    callback(new Error(e.message))

                }

            }
            address = bsv.Address.Testnet.fromPrivKey(privateKey);

        } else {
            privateKey = bsv.PrivKey.fromString(key);
            address = bsv.Address.fromPrivKey(privateKey);
        }

        let offset = 0
        if (!options.hasOwnProperty("utxos")) {
            explorer.utxos(address.toString(), 0, 100)
                .then((res) => {
                    let utxos = res.unspent || res
                    options.debug ? console.table(utxos) : false;
                    makeTx(builder, address, script, options, utxos, callback)

                })
        } else if (Array.isArray(options.utxos)) {
            //console.log("we already got utxos")
            makeTx(builder, address, script, options, options.utxos, callback)

        } else {
            callback(new Error("utxos must be an array"))

        }

    } else {
        // No pay, no key
        // Only outputs will be created        
        let tx = new bsv.Tx()
        if (options.tx) {
            // options.tx can be hexstring or buffer

            tx = typeof options.tx === "string" ? bsv.Tx.fromHex(options.tx) : options.tx.hasOwnProperty("byteLength") ? bsv.Tx.fromBr(new bsv.Br(options.tx)) : options.tx

        }
        let builder = new bsv.TxBuilder(tx);


        let feeKb = options.hasOwnProperty("feeKb") ? options.feeKb : 1;
        let dust = options.hasOwnProperty("dust") ? options.dust : 0;
        builder.setFeePerKbNum(feeKb)
        builder.dust = dust

        if (script) {
            builder.outputToScript(new bsv.Bn(options.dataSats), script);
        }
        if (options.hasOwnProperty("lock")) {
            builder.setNLocktime(options.lock)
        }
        if (options.nData) {
            options.safe = !options.hasOwnProperty("safe") ? true : false;
            options.opReturn = !options.hasOwnProperty("opReturn") ? true : false;

            options.nData.forEach((data) => {
                try {
                    builder.outputToScript(new bsv.Bn(options.dataSats), _script({ safe: options.safe, opReturn: options.opReturn, data }));
                } catch (e) {
                    callback(new Error(e.message))

                }

            })
        }

        if (options.pay && Array.isArray(options.pay.to)) {
            options.pay.to.forEach(function(receiver) {

                let dAddress = ""
                if (options.testnet) {
                    dAddress = bsv.Address.Testnet.fromString(receiver.address)
                } else {
                    dAddress = bsv.Address.fromString(receiver.address)
                }
                builder.outputToAddress(new bsv.Bn(receiver.value), dAddress)
            })
        }
        builder.buildOutputs()

        // Adding option to return tx on bsv format if specified
        let returnThis = options.format === "hex" ? builder.tx.toHex() : builder.tx;
        callback(null, returnThis);


    }
}
var send = function(options, callback) {
    if (!callback) {
        callback = function() {};
    }

    build(options, function(err, tx) {
        if (err) {
            callback(err);
            return;
        }
        let network = options.testnet ? "test" : "main";
        if (!options.api) options.api = "bitails"
        const explorer = new Explorer(network, { api: options.api })
        explorer.broadcast(tx.toHex())
            .then((latx) => {
                callback(null, latx)
            }).catch((e) => { callback(e, null) })

    })
}
// compose script
var _script = function(options) {

    var s = null;
    if (options.data) {
        s = new bsv.Script();
        options.safe = !options.hasOwnProperty("safe") ? true : options.safe;
        options.opReturn = !options.hasOwnProperty("opReturn") ? true : options.opReturn;
        if (options.safe) { s.writeOpCode(bsv.OpCode.OP_FALSE) }
        if (options.opReturn) { s.writeOpCode(bsv.OpCode.OP_RETURN) }

        [...options.data].forEach(function(item) {
            // add push data
            if (item.constructor.name === 'ArrayBuffer') {
                let buffer = _Buffer.from(item)
                s.writeBuffer(buffer)
            } else if (item.constructor.name === 'Buffer') {
                s.writeBuffer(item)
            } else if (typeof item === 'string') {
                if (/^0x/i.test(item)) {
                    // ex: 0x6d02
                    s.writeBuffer(Buffer.from(item.slice(2), "hex"))
                } else {
                    // ex: "hello"
                    s.writeBuffer(Buffer.from(item))
                }
            } else if (typeof item === 'object' && item.hasOwnProperty('op')) {
                if (typeof item.op === "number") {
                    s.writeOpCode(item.op)
                } else if (/^0x/i.test(item.op)) {
                    s.writeBuffer(Buffer.from(item.op.substring(2, item.op.length), "hex"))
                } else if (typeof item.op === "string") {
                    if (item.op.substring(0, 3) === "OP_") {
                        s.writeOpCode(new bsv.OpCode().fromString(item.op))
                    } else {
                        s.writeOpCode(new bsv.OpCode().fromNoOpString(item.op))
                    }
                }


            }
        })


        /*} else if (typeof options.data === 'string') {
            // Exported transaction 
            s = bsv.Script.fromString(options.data);
        }*/
    }
    return s;
}
var connect = function(network = "main", options = { api: "bitails" }) {
    return new Explorer(network, options);
}
module.exports = {
    build,
    send,
    connect,
    bsv
}